@media (orientation: portrait) {
  body {
    .root {
      .widjet-container {
        box-shadow: none;
        width: 100%;

        .widjet {
          width: 100%;

          .mainMenu {
            width: 100%;
            background-color: rgba(255, 255, 255, 0);
            padding: 0;
            box-shadow: none;

            .users {
              width: 100%;
            }
          }

          .cards {
            .card {
              height: 85px;
              width: 85px;
            }
          }
        }
      }
    }
  }
}